@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.title{
    text-align: center;
    background-color: #102A43;
    padding: 40px 0;
}
.title > h1{
    margin: 0;
    color: #F0F4F8;
    font-size: 3em;
    text-shadow: 2px 1px 0px #0303, 3px 2px 0px #0303, 4px 3px 0px #0303, 5px 4px 0px #0303;
}
.board{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    margin-top: 30px;
    margin-bottom: 30px;
}
.ToolBox{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    background-color: #bdbdbd;
}
.twitter-picker {
    border: none !important;
    background-color: inherit !important;
    box-shadow: none !important;
}
.Dimensions{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}
.Dimensions-Input{
    width: 35px;
    font-size: 14px;
    color: rgb(102, 102, 102);
    border: 0px;
    outline: none;
    height: 28px;
    box-sizing: content-box;
    box-shadow: rgb(240 240 240) 0px 0px 0px 1px inset;
    border-radius: 4px;
    margin: 0 5px;
    padding-left: 8px;
}

.Dimensions-Separator{
    font-weight: bold;
    margin: 0;
}
.Tools{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    min-width: 65px;
    padding-right: 8px;
}

.Tools-Container{
    height: 40px;
    width: 40px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    border-radius: 4px;
}

.Tools-Selected{
    background-color: grey;
}

.Tools-Tool{
    width: 25px;
    height: 25px;
}
body{
    font-family: Roboto;
    background-color: #333;
}
